.button {
  @include button;

  &-service {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 280px;
    height: 50px;
    border-radius: 10px;
    text-decoration: none;
    user-select: none;
    transition: box-shadow .3s;

    &__icon {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 20px 0 0;
      width: 24px;
      height: 24px;

      svg {
        width: 100%;
        height: 100%;
        fill: $c-white;
      }
    }

    &_red {
      background: linear-gradient(45deg, #9B0006, #D30020);

      &:hover, &.active {
        box-shadow: 0 5px 40px rgba(208, 0, 31, .55);
      }

      &:active {
        box-shadow: none;
      }
    }

    &_blue {
      background: #405AA0;

      &:hover, &.active {
        box-shadow: 0 5px 40px rgba(64, 90, 160, .55);
      }

      &:active {
        box-shadow: none;
      }
    }

    &_light-blue {
      background: #94B7E4;

      &:hover, &.active {
        box-shadow: 0 5px 40px rgba(148, 183, 228, .55);
      }

      &:active {
        box-shadow: none;
      }
    }
  }
}
