.catalog {
  display: flex;

  &-mb {
    margin: 0 0 28px;
  }

  &__col {
    &:first-child {
      flex-shrink: 0;
      width: 280px;
    }

    &:last-child {
      flex-grow: 1;
      padding-left: 60px;
    }
  }

  &-sort {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 60px 0;
  }

  &-available {
    @include fz(18px);
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }

  &-group-sort {
    display: flex;
    align-items: baseline;

    &__head {
      @include fz(15px);
      color: $c-blue;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }
  }

  &-service {
    display: flex;
    justify-content: space-between;

    button.button {
      width: 280px;
    }
  }

  &-map {
    position: relative;
    height: 120px;
    margin: 0 0 60px;
    border-radius: 10px;
    background: url("../../img/catalog/map@2x.jpg") center;
    background-size: cover;
    overflow: hidden;
    transition: height .3s;

    &__btn {
      position: absolute;
      top: 30px;
      right: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 60px;
      height: 60px;
      border-radius: 50%;
      background: linear-gradient(45deg, #3C589E, #93B7E4);
      border: none;
      cursor: pointer;

      &:hover {
        background: linear-gradient(45deg, #4D6FB8, #AACFEF);
      }
    }

    &.open {
      height: 460px;
    }
  }
}

.custom-select {
  width: 100%;
  height: auto;
  border: none;
  border-bottom: 2px solid rgba(187, 187, 187, .5);
  background-color: $c-main-bg;

  & > a {
    @include fz(14px);
    width: 100%;
    padding: 12px 0;
    color: #B6B6B6;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }

  & > div {
    transform: translateY(-30px);
    border: none;
    margin: 0;
    background-color: $c-main-bg;
  }

  & input {
    width: 100%;
    margin: 0;
    background-color: $c-main-bg;
    border: none;
    border-bottom: 2px solid rgba(187, 187, 187, 0.5);
  }

  & input + div {
    overflow: auto !important;
  }

  & > div > div {
    max-height: 250px;
    margin: 10px 0 0;
    padding: 10px 0;
  }

  & div ul {
    box-shadow: 0 20px 45px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    background-color: $c-white;
    overflow: hidden;
  }

  & div ul li {
    @include fz(18px);
    padding: 10px 30px;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;

    &:hover {
      color: $c-dark;
      background-color: #F3F3F3;
    }
  }
}